.header_section {
  margin-left: 0;
  position: relative; /* Added for positioning */
}

.header_section img {
  width: 98.7vw;
  height: 40vh;
  object-fit: cover;
  margin: 0;
}

.header_section h1 {
  font-size: 5em;
  color: #0f2e0b;
  position: absolute;
  top: 20vh;
  left: 50%;
  transform: translateX(-50%);
}

.signup_container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 60vh;
  margin-top: 10px; /* Adjust margin as needed */
}

.signup_container form {
  width: 60%; /* Adjust width of the form */
  padding: 20px;
  /* border: 1px solid #ccc; */
  border-radius: 8px;
  /* box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); */
  align-items: center;
  /* background-color:#cbb89d; */
}

.signup_container form label {
  display: block;
  margin-bottom: 5px;
  font-weight: 600;
  color: #5d2510;
  font-size: 20px;
}

.signup_container form input {
  padding: 8px;
  margin-bottom: 10px;
  border: 1px solid #fff5e8;
  background-color: #fffcf8;
  border-radius: 4px;
  width: 100%;
  box-sizing: border-box;
}

.signup_container form button {
  padding: 10px;
  margin-top: 20px;
  font-size: 20px;
  border: none;
  border-radius: 4px;
  width: 280px;
  background-color: #6a6f4c; /* Button color */
  color: #fff5e8;
  cursor: pointer;
  float: right;
}

.signup_container form button:hover {
  background-color: #5b6040; /* Button hover color */
}

.signup_container p {
  margin-top: 10px;
  color: green; /* Success message color */
}
